import React, { useEffect } from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter as Router } from 'react-router-dom';
import { Provider } from 'react-redux';
import store from './store';
import App from 'src/views/App';

import setAuthToken from './redux/utils/setAuthToken';
import { loadUser } from './redux/auth/thunks';

import { ConversationsProvider, useConversations } from './Chat/contexts/ConversationsProvider';
import { SocketProvider } from './Chat/contexts/SocketProvider';
import { ContactsProvider } from './Chat/contexts/ContactsProvider';

// import configureStore from 'src/state/store';
//  import client from 'src/config/client';
// import { loadUser } from "./redux/auth/thunks";
// import setAuthToken from "./redux/utils/setAuthToken";
const Container = () => {
    // const reduxStore = configureStore(window.REDUX_INITIAL_DATA);
    useEffect(() => {
        if (localStorage.id !== undefined) {
            setAuthToken(localStorage.token, localStorage.id);
            store.dispatch(loadUser());
        }
    }, []);
    const id = localStorage.getItem('id');
    return (
        <ContactsProvider>
            <SocketProvider id={id}>
                <ConversationsProvider>
                    <Provider store={store}>
                        <Router>
                            <App />
                        </Router>
                    </Provider>
                </ConversationsProvider>
            </SocketProvider>
        </ContactsProvider>

    );
};

ReactDOM.render(<Container />, document.getElementById('root'));
